@import "/styles/variables.scss";

header {
  z-index: 2;
  width: 100%;
  background-image: linear-gradient(to bottom, #ffffff 25%, rgba(255, 255, 255, 0));
  justify-content: space-around;
  position: absolute;
  top: 0;
  font-weight:600;
  @media (max-width: $breakpoint-me-max) {
    background-color: $color-white;
    position: relative;
  }
  & .nav-main {
    /* logo */
    & .nav-logo {
      @media (max-width: $breakpoint-me-max) {
        padding: 1rem 1.5rem;
      }
      & #logo-wrapper {
        grid-column-start: 1;
        grid-column-end: 4;
        @media (max-width: $breakpoint-me-max) {
          grid-column-end: 3;
        }
        & img {
          display: inline-block;
          vertical-align: middle;
          max-width: 14.3125rem;
          @media (max-width: $breakpoint-me-max) {
            max-width: 8.625rem;
          }
        }
      }
    }

    /* menu toggle on mobile/tablet */
    & #nav-toggle {
      display: none;
      @media (max-width: $breakpoint-me-max) {
        display: block;
        grid-column-start: 4;
        grid-column-end: 4;
        justify-self: flex-end;
      }
      &.close {
        & #hamburger-box {
          & .hamburger-inner {
            background-color: $color-pink;
            transform: rotate(45deg) translate(-7px, -7px);
            &:first-child {
              transform: rotate(-45deg) translate(-6px, 5px);
            }
            &:nth-child(2) {
              opacity: 0
            }
          }
        }
      }
      & #hamburger-box {
        display: flex;
        flex-direction: column;
        width: 1.75rem;
        transition: 0.3s;
        & .hamburger-inner {
          background-color: $color-black;
          width: 1.75rem;
          height: 0.1875rem;
          &:nth-child(2) {
            margin: 0.375rem 0;
          }
        }
      }
    }

    /* menu */
    & #navigation-wrapper {
      grid-column-start: 4;
      grid-column-end: span 9;
      @media (max-width: $breakpoint-me-max) {
        display: none;
        background-color: $color-grey;
        padding: 0.75rem 1.5rem 1.5rem;
      }
      &.open {
        display: block;
      }
      & #navigation {
        grid-column-start: 4;
        grid-column-end: span 12;
        @media (max-width: $breakpoint-me-max) {
          grid-column-start: 1;
          grid-column-end: span 4;
        }
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        @media (max-width: $breakpoint-me-max) {
          flex-direction: column;
        }
        /* menu elements */
        & li {
          display: block;
          margin-left: 3.125rem;
          position: relative;
          cursor: pointer;
          padding: 1.25rem 1.25rem 1.25rem 0;
          &:first-child {
            margin-left: 0;
            border: none;
          }
          &.navigation-cta {
            border: 0;
            padding-right: 0;
            & .button {
              width: 100%;
            }
          }
          @media (max-width: $breakpoint-me-max) {
            width: 100%;
            padding: 0;
            margin: 0;
            border-top: 1px solid $color-mid-grey;
          }
          & .navigation-link {
            display: block;
            color: $color-black;
            position: relative;
            border-bottom: 2px solid transparent;
            @media (max-width: $breakpoint-me-max) {
              width: 100%;
              padding: 1.25rem 0;
            }
            &:hover, &:active, &:focus {
              color: $color-pink;
              &:after {
                filter: $filter-pink;
              }
            }
            @media (min-width: $breakpoint-lg-min) {
              &.active {
                color: $color-black;
                border-bottom: 2px solid $color-pink;
              }
            }
          }

          /* chevron only for elements with submenu and sub-elements */
          &.parent {
            & .navigation-link:after {
              overflow: hidden;
              content: "";
              background: url("/images/chevron.svg") no-repeat right center;
              width: 8px;
              height: 13px;
              position: absolute;
              right: 0;
              top: 50%;
              transform: translateY(-50%);

            }
            & > .navigation-link:after {
              right: -1rem;
              transform: translateY(-50%) rotate(90deg);
              @media (max-width: $breakpoint-me-max) {
                right: 0;
              }
            }
            &.open > .navigation-link {
              color: $color-pink;
              &:after {
                filter: $filter-pink;
              }
            }
          }

          /* submenu */
          & .sub-navigation {
            display: none;
            @media (max-width: $breakpoint-me-max) {
              & li {
                border: 0;
                padding-right: 0;
                & .navigation-link {
                  padding: 0.5rem 0;
                }
              }
            }
            @media (min-width: $breakpoint-lg-min) {
              position: absolute;
              white-space: nowrap;
              z-index: 1;
              border-radius: 0.5rem;
              box-shadow: 0 0.75rem 0.875rem 0 rgba(60, 75, 110, 0.16);
              background-color: $color-grey;
              top: 3.5rem;
              left: -65%;
              &:before {
                bottom: 100%;
                left: 50%;
                border: solid transparent 0.5rem;
                content: " ";
                height: 0;
                width: 0;
                position: absolute;
                border-bottom-color: $color-grey;
                margin-left: -0.5rem;
              }
              & li {
                border-radius: 0 0 0.5rem 0.5rem;
                margin: 0;
                padding: 0 1rem;
                background-color: $color-white;
                & .navigation-link {
                  padding: 1.25rem 1.25rem 1.25rem 0;
                }
                &:first-child {
                  background-color: $color-grey;
                  border-radius: 0.5rem 0.5rem 0 0;
                }

              }
            }
          }
          &.open > .sub-navigation {
            display: block;
          }
        }
      }
    }
  }
}