@import "/styles/variables.scss";

whatwedo {
  #whatwedo__wrapper {
    background: linear-gradient(to right, $color-pink 0%, $color-pink 18%, transparent 18%, transparent 100%) no-repeat bottom left;
    background-size: 100% 38.225rem;
    @media (min-width: $breakpoint-xlg-min) {
      background: linear-gradient(to right, $color-pink 0%, $color-pink 26%, transparent 26%, transparent 100%) no-repeat bottom left;
      background-size: 100% 38.225rem;
    }
    @media (min-width: $breakpoint-xlg-max) {
      background: linear-gradient(to right, $color-pink 0%, $color-pink 33%, transparent 33%, transparent 100%) no-repeat bottom left;
      background-size: 100% 38.225rem;
    }
    @media (min-width: $breakpoint-xxlg-min) {
      background: linear-gradient(to right, $color-pink 0%, $color-pink 38%, transparent 38%, transparent 100%) no-repeat bottom left;
      background-size: 100% 38.225rem;
    }
    @media (min-width: $breakpoint-xxlg-max) {
      background: linear-gradient(to right, $color-pink 0%, $color-pink 42%, transparent 42%, transparent 100%) no-repeat bottom left;
      background-size: 100% 38.225rem;
    }
    .cards {
      background-color: $color-white;
    }
  }
  #whatwedo__shape {
    position: absolute;
    bottom: 0;
    left: -0.05rem;
    width: 0;
    height: 0;
    z-index: 1;
    border-style: solid;
    border-width: 38.275rem 0 0 16.375rem;
    border-color: transparent transparent transparent $color-pink;
    @media (max-width: $breakpoint-me-max) {
      border-width: 14rem 0 0 6.375rem;
    }
  }
}