@import "/styles/variables.scss";

newsletter {
  #newsletter__wrapper {
    padding: 3rem 0;
    @media (max-width: $breakpoint-me-max) {
      padding: 2rem 1.5rem;
    }
    & .container--lg {
      align-items: flex-start;
    }
    .newsletter__block {
      grid-column: auto / span 4;
      padding: 1.5rem;
      border-radius: 8px;
      box-shadow: 0 0.75rem 0.875rem 0 rgba(60, 75, 110, 0.16);
      @media (max-width: $breakpoint-me-max) {
        margin-bottom: 1.5rem;
      }
      &:first-child {
        border: none;
        box-shadow: none;
        padding: 1.5rem 0 0;
        @media (max-width: $breakpoint-me-max) {
          padding:0;
        }
        & p {
          margin-top: 1rem;
        }
      }
      & .newsletter__cta {
        margin-top: 1.5rem;
      }
    }
  }
}