/* colors */
$color-white: #FFFFFF;
$color-black: #000000;
$color-pink: #E6007E;
$color-pink-hover: #B30062;
$color-deep-blue: #3C4B6E;
$color-deep-blue-hover: #29344C;
$color-dark-blue: #0C2545;
$color-blue: #0064D2;
$color-sky-blue: #72CBFF;
$color-light-blue: #BEE1FF;
$color-mid-grey: #D6D6D6;
$color-grey: #F5F5F5;
$color-purple: #400E50;

/* fonts */
$font-poppins: Poppins;
@font-face {
  font-family: $font-poppins;
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: $font-poppins;
  font-style: normal;
  font-weight: 600;
}

/* filters */
$filter-pink: invert(19%) sepia(100%) saturate(3354%) hue-rotate(313deg) brightness(83%) contrast(121%);
$filter-white: invert(100%) sepia(100%) saturate(0%) hue-rotate(321deg) brightness(100%) contrast(300%);

/* breakpoints */
$breakpoint-sm-min: 360px;
$breakpoint-sm-mid: 528px;
$breakpoint-sm-max: 767px;
$breakpoint-me-min: 768px;
$breakpoint-me-max: 1023px;
$breakpoint-lg-min: 1024px;
$breakpoint-lg-mid: 1200px;
$breakpoint-lg-max: 1440px;
$breakpoint-xlg-min: 2250px;
$breakpoint-xlg-max: 3000px;
$breakpoint-xxlg-min: 4239px;
$breakpoint-xxlg-max: 5999px;

