@import "/styles/variables.scss";

partners {
  #partners__wrapper {
    padding: 3.5rem 0;
    background-color: rgba($color-light-blue, 20%);
    @media (max-width: $breakpoint-me-max) {
      padding: 2rem 0;
    }
    #partners {
      grid-column-start: 1;
      grid-column-end: span 12;
      text-align: center;
      #partners__header {
        margin-bottom: 3rem;
        @media (max-width: $breakpoint-me-max) {
          margin-bottom: 1.5rem;
        }
      }
      #partners__content {
        justify-items: center;
        align-items: center;
        margin-bottom: 2rem;
        @media (max-width: $breakpoint-me-max) {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          padding: 0 2rem;
          margin-bottom: 1.5rem;
        }
        div {
          grid-column: auto / span 2;
          @media (max-width: $breakpoint-me-max) {
            width: 33%;
          }
          img {
            max-width: 100%;
          }
        }
      }
    }
  }
}