@import "/styles/variables.scss";

ourwork {
  #ourwork__wrapper {
    position: relative;
    width: 100%;
    overflow: hidden;
    .ourwork__shape {
      position: absolute;
      top: 0;
      right: -3rem;
      width: 0;
      height: 0;
      border-style: solid;
      &--pink {
        background-color: $color-pink;
        transform: skew(21deg);
        width: 4.125rem;
        height: 100%;
        z-index: 1;
      }
    }
    @media (max-width: $breakpoint-me-max) {
      .ourwork__shape {
        position: absolute;
        top: 0;
        right: 0;
        width: 0;
        height: 0;
        border-style: solid;

        &--pink {
          background-color: transparent;
          transform: none;
          z-index: 1;
          border-color: transparent $color-pink transparent transparent;
          border-width: 0 2.1875rem 5.3125rem 0;
          top: 2.5rem;
        }
      }
    }
  }
}