@import "/styles/variables.scss";

.cards {
  position: relative;
  padding: 3.5rem 0;
  @media (max-width: $breakpoint-me-max) {
    padding: 0;
  }
  .cards__row {
    grid-column-start: 1;
    grid-column-end: span 12;
    z-index: 2;
    @media (max-width: $breakpoint-me-max) {
      grid-column-end: span 4;
      padding: 1.5rem 1.5rem 1rem;
    }
    &--header {
      padding: 0 6.375rem 3rem;
      @media (max-width: $breakpoint-me-max) {
        padding: 0 0 1.5rem;
      }
      & h2 {
        margin-bottom: 0.8125rem;
      }
    }
    &--boxes {
      display: flex;
      @media (max-width: $breakpoint-sm-max) {
        flex-direction: column;
      }
      .cards__box {
        width: 33%;
        margin-left: 2rem;
        border-radius: 0.5rem;
        box-shadow: 0 0.75rem 0.875rem 0 rgba(60, 75, 110, 0.16);
        background-color: $color-white;
        overflow: hidden;
        position: relative;
        @media (max-width: $breakpoint-sm-max) {
          width: 100%;
          margin-left: 0;
          margin-bottom: 1.5rem;
        }
        &:first-child {
          margin-left: 0;
        }
        & .cards__image-wrapper {
          height: 17.13rem;
          position: relative;
          cursor:pointer;
          @media (max-width: $breakpoint-lg-mid) {
            height: auto;
            max-height: 17.13rem;
          }
          & .cards__image {
            height: 100%;
            overflow: hidden;
            @media (max-width: $breakpoint-me-max) {
              height: auto;
              max-height: 17.13rem;
            }
            & img {
              display:block;
              width: 100%;
            }
          }
          & .cards__hook {
            border-radius: 0.875rem;
            background-color: $color-pink;
            color: $color-white;
            padding: 0.2rem 0.5rem;
            font-size: 0.8125rem;
            position: absolute;
            top: calc(100% - 1rem);
            margin-left: 1.5rem;
            @media (max-width: $breakpoint-me-max) {
              margin-left: 1rem;
            }
          }
        }
        & .cards__content {
          padding: 1rem 1.5rem 5rem;
          height: calc(100% - 17.13rem);
          @media (max-width: $breakpoint-me-max) {
            height: auto;
            padding: 1rem 1rem 5rem;
          }
          & p {
            padding: 0.5rem 0 1rem;
          }
        }
        &--with-hook .cards__content {
          padding-top:1.5rem;
        }
        & .cards__cta {
          position: absolute;
          bottom: 1.5rem;
          left: 1.5rem;
          @media (max-width: $breakpoint-me-max) {
            left: 1rem;
          }
        }
      }
    }
  }
}