@import "/styles/variables.scss";

footer {
  background-color: $color-deep-blue;
  color: $color-white;
  #footer__wrapper {
    padding: 3.5rem 7.5rem;
    @media (max-width: $breakpoint-me-max) {
      padding: 0 1.5rem 2.0625rem;
    }
    #footer {
      height: 12.5rem;
      @media (max-width: $breakpoint-me-max) {
        height: auto;
      }
      .footer__column {
        grid-column-start: 9;
        grid-column-end: span 4;
        align-self: flex-start;
        &.footer__column-bottom {
          align-self: flex-end;
        }
        @media (max-width: $breakpoint-me-max) {
          grid-column-start: 1;
          grid-column-end: span 4;
          margin-top: 2.0625rem;
        }
        &:nth-child(odd) {
          grid-column-start: 1;
          grid-column-end: span 8;
          @media (max-width: $breakpoint-me-max) {
            grid-column-start: 1;
            grid-column-end: span 4;
          }
        }
        #footer__logo {
          display: inline-block;
          margin-bottom: 0.2rem;
          @media (max-width: $breakpoint-me-max) {
            margin-bottom: 1rem;
          }
        }
        .footer__column-link {
          & li {
            margin-bottom: 0.5rem;
            & a:not(.link--white) {
              color: $color-white;
              cursor: pointer;
              &:hover, &:active, &:focus {
                text-decoration: underline;
              }
            }
          }
        }
        #footer__column-social {
          display: flex;
          flex-direction: row;
          height: 2rem;
          & li {
            margin-left: 1.5rem;
            &:first-child {
              margin-left: 0;
            }
            & a {
              display: inline-block;
              background-color: $color-white;
              border-radius: 1rem;
              width: 2rem;
              height: 2rem;
              text-align: center;
              position: relative;
              cursor: pointer;
              &:hover, &:active, &:focus {
                background-color: $color-pink;
                .icon {
                  filter: $filter-white;
                }
              }
              &.icon-youtube-wrapper {
                border-radius: 0.25rem;
                height: 1.5rem;
                margin: 0.25rem 0;
              }
              .icon {
                display: inline-block;
                width: 2rem;
                height: 2rem;
                position: absolute;
                top: 0;
                left: 0;
                background-repeat: no-repeat;
                background-position: center;
              }
              .icon-twitter {
                background-image: url(/images/footer/sociallinks/icon-twitter.svg);
              }
              .icon-instagram {
                background-image: url(/images/footer/sociallinks/icon-instagram.svg);
              }
              .icon-linkedin {
                background-image: url(/images/footer/sociallinks/icon-linkedin.svg);
              }
              .icon-youtube {
                background-image: url(/images/footer/sociallinks/icon-youtube.svg);
                height: 1.5rem;
              }
              .icon-vimeo {
                background-image: url(/images/footer/sociallinks/icon-vimeo.svg);
              }
            }
          }
        }
        #footer__column-copyright {
          font-size: 0.8125rem;
        }
      }
    }
  }
}