@mixin button($color, $colorHover) {
  z-index:1;
  overflow:hidden;
  position: relative;
  cursor: pointer;
  display: inline-block;
  padding: 1rem;
  font-size: 1rem;
  font-weight:600;
  line-height: 1.5rem;
  border-radius: 0.25rem;
  color: $color-white;
  background:$colorHover;
  transition: background-size .3s, color .3s;
  text-align: center;
  &:before {
    display: block;
    content: '';
    z-index:2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:$color;
    z-index: -1;
    transform-origin:top left;
    transform:translateX(0%) skewX(-45deg);
    transition: transform .3s;
  }
  &:after {
    display: block;
    content: '';
    z-index:2;
    position: absolute;
    top: 0;
    right:0;
    width: 100%;
    height: 100%;
    background:$color;
    z-index: -1;
    transform-origin:bottom right;
    transform:translateX(0%) skewX(-45deg);
    transition: transform .3s;
  }
  &:hover {
    &:before {
      transform: translateX(-100%) skewX(-45deg);
    }
    &:after {
      transform: translateX(100%) skewX(-45deg);
    }
  }
}

@mixin container {
  display: grid;
  column-gap: 2.1875rem;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

@mixin link($color) {
  background: linear-gradient(to bottom, transparent 40%, rgba($color, .15) 40%);
  background-size: 100% 200%;
  background-position: left top;
  transition: all .3s ease;
  color: $color;
  cursor: pointer;
  &:hover {
    background-position: left bottom;
  }
}