@import "/styles/variables.scss";

banner {
  /* carousel */
  #carousel-wrapper {
    z-index: 1;
    position: relative;
    max-height: 39.375rem;
    overflow: hidden;
    #carousel {
      /* whole slide */
      .carousel-item {
        position: absolute;
        max-height: 39.375rem;
        top: 0;
        width: 100%;
        margin: auto;
        transition: transform .5s, opacity .5s, z-index .5s;
        opacity: 0;
        z-index: 1;
        &.active {
          opacity: 1;
          position: relative;
          z-index: 2;
        }
        /* slide's image */
        .carousel-image {
          overflow: hidden;
          max-height: 39.375rem;
          @media (max-width: $breakpoint-me-max) {
            display: flex;
            justify-content: center;
            align-items: center;
            max-height: 14.375rem;
          }
          & img {
            width: 100%;
            @media (max-width: $breakpoint-sm-mid) {
              width: 100%;
              height: 100%;
              object-position: center;
            }
            @media (min-width: $breakpoint-sm-mid) and (max-width: $breakpoint-me-max) {
              flex-shrink: 0;
              min-width: 100%;
              min-height: 100%;
            }
          }
        }
        /* slide's overlay (text) */
        .carousel-overlay {
          width: 100%;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          @media (max-width: $breakpoint-me-max) {
            position: relative;
            top: auto;
            transform:none;
          }
          & > div {
            @media (max-width: $breakpoint-me-max) {
              padding: 1rem 1.5rem;
            }
            p {
              grid-column-start: 4;
              grid-column-end: 12;
              margin-top: -2.5rem;
              margin-left: 2.6rem;
              white-space: nowrap;
              z-index: 2;
              @media (min-width: $breakpoint-lg-min) and (max-width: $breakpoint-lg-mid) {
                margin-top: -2.5rem;
                margin-left: 2.69rem;
              }
              @media (max-width: $breakpoint-me-max) {
                grid-column-start: 1;
                grid-column-end: 4;
                margin-top: -2.2rem;
                margin-left: 0.8625rem;
              }
              &:first-child {
                grid-column-start: 1;
                grid-column-end: span 4;
                margin: 0;
              }
            }
            .carousel-separator {
              grid-column-start: 4;
              grid-column-end: 12;
              background-color: $color-pink;
              transform: skew(-27deg);
              width: 0.75rem;
              height: 3.5rem;
              margin-top: -2.125rem;
              margin-left: 2.6875rem;
              z-index: 1;
              @media (min-width: $breakpoint-lg-min) and (max-width: $breakpoint-lg-mid) {
                margin-top: -2.1rem;
                margin-left: 2.75rem;
              }
              @media (max-width: $breakpoint-me-max) {
                grid-column-start: 1;
                grid-column-end: 2;
                width: 0.5rem;
                height: 2.5rem;
                margin-top: -0.25rem;
                margin-left: 0.6875rem;
              }
            }
          }
        }
      }
    }
    /* carousel's pagination & hooks */
    #carousel-footer-wrapper {
      position: absolute;
      bottom: 0;
      z-index: 10;
      width: 100%;
      background-image: linear-gradient(to top, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0));
      padding: 1rem 0;
      @media (max-width: $breakpoint-me-max) {
        position:relative;
        padding:0;
      }
      #carousel-pagination-wrapper {
        width: 75rem;
        margin: 0 auto;
        @media (min-width: $breakpoint-lg-min) and (max-width: $breakpoint-lg-mid) {
          padding:0 1.25rem;
        }
        @media (max-width: $breakpoint-me-max) {
          padding: 0 1.5rem;
        }
        /* carousel's pagination */
        #carousel-pagination {
          float: left;
          .carousel-pager {
            width: 1rem;
            height: 1rem;
            background-color: $color-white;
            border-radius: 10px;
            cursor: pointer;
            float: left;
            margin-left: 1rem;
            @media (max-width: $breakpoint-me-max) {
              background-color: $color-mid-grey;
            }
            &:first-child {
              margin-left: 0;
            }
            &.active, &:hover, &:active, &:focus {
              background-color: $color-pink;
            }
          }
        }
        /* carousel's hooks */
        #carousel-hooks {
          float: left;
          margin-left: 1rem;
          .carousel-hook {
            position: absolute;
            opacity: 0;
            font-weight: 600;
            &.active {
              position: relative;
              opacity: 1;
            }
          }
        }
      }
    }
  }
}