@import "./variables";
@import "mixins";

* {
  box-sizing: border-box;
}

body {
  font-family: $font-poppins, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5rem;

  /* titles */
  h1, .h1-like {
    font-weight: 600;
    font-size: 3rem;
    line-height: 4rem;
    @media (max-width: $breakpoint-me-max) {
      font-size: 1.75rem;
      line-height: 2.5rem;
    }
  }
  h2, .h2-like {
    font-weight: 600;
    font-size: 2rem;
    line-height: 2.75rem;
    @media (max-width: $breakpoint-me-max) {
      font-size: 1.5rem;
      line-height: 2.25rem;
    }
  }
  h3, .h3-like {
    font-weight: 600;
    font-size: 1.75rem;
    line-height: 2.5rem;
    @media (max-width: $breakpoint-me-max) {
      font-size: 1.25rem;
      line-height: 2rem;
    }
  }
  h4, .h4-like {
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 2.25rem;
    @media (max-width: $breakpoint-me-max) {
      font-size: 1.125rem;
      line-height: 1.75rem;
    }
  }
  h5, .h5-like {
    font-weight: 600;
    font-size: 1.25rem;
    line-height: 1.75rem;
    @media (max-width: $breakpoint-me-max) {
      font-size: 1rem;
      line-height: 1.5rem;
    }
  }

  /* links */
  a {
    text-decoration: none;
    color: $color-black;
  }
  .link {
    &--pink {
      @include link($color-pink);
    }
    &--white {
      @include link($color-white);
    }
  }

  /* buttons */
  .button {
    &--pink {
      @include button($color-pink, $color-pink-hover);
    }
    &--blue {
      @include button($color-deep-blue, $color-deep-blue-hover);
    }
  }

  .outer-limit {
    max-width: 90rem;
    margin: 0 auto;
  }
  .container {
    &--lg {
      @media (min-width: $breakpoint-lg-min) {
        @include container;
        max-width: 75rem;
        grid-template-columns:repeat(12, 1fr);
        padding:0 1.25rem;
      }
    }
    &--sm {
      @media (max-width: $breakpoint-me-max) {
        @include container;
        grid-template-columns:repeat(4, 1fr);
        column-gap: 1.1875rem;
      }
    }
  }
}